    <el-menu
      default-active="2"
      class="el-menu-vertical-demo el-menu"
      text-color="#000"
      active-text-color="rgba(209, 0, 23, 1)"
      :collapse="isCollapse"
      :collapse-transition="false"
    >
      <el-submenu index="1">
        <template slot="title">
          <i class="el-icon-location"></i>
          <span>资源</span>
        </template>
        <el-menu-item-group>
          <el-menu-item index="1-1">
            <router-link to="/book">书籍教材</router-link>
          </el-menu-item>
          <el-menu-item index="1-2">
            <router-link to="/book/1">考试资料</router-link>
          </el-menu-item>
          <el-menu-item index="1-3">课堂资料</el-menu-item>
          <el-menu-item index="1-3">作业参考</el-menu-item>
        </el-menu-item-group>
      </el-submenu>
      <el-menu-item index="2">
        <i class="el-icon-menu"></i>
        <span slot="title">项目</span>
      </el-menu-item>
      <el-menu-item index="3">
        <i class="el-icon-document"></i>
        <span slot="title">实验</span>
