      </el-menu-item>
    </el-menu>
  </div>
</template>
<script>
export default {
  name: "Sider",
  data() {
    return {
      collapsed: false,
      selectedKeys: ["1"],
      openKeys: ["sub1"],
      preOpenKeys: ["sub1"],
      isCollapse: false,
    };
  },
  watch: {
    openKeys(val, old) {
      this.preOpenKeys = old;
    },
  },
  methods: {
    toggleCollapsed() {
      this.collapsed = !this.collapsed;
      this.openKeys = this.collapsed ? [] : this.preOpenKeys;
    },
    toggleMenu() {
      this.isCollapse = !this.isCollapse;
    },
  },
};
</script>
<style scoped>
.sider {
  display: flex;
