      <div
        class="kitty"
        tabindex="0"
        @mouseenter="mouseenter"
        @mouseout="mouseout"
      ></div>
      <div ref="payBox" class="payBox" hidden>
        <img src="../assets/reward.png" id="reward-img" alt="" />
        <p id="reward-desc">觉得不错，投喂作者</p>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Reward",
  data() {
    return {};
  },
  methods: {
    mouseenter() {
      this.$refs.payBox.removeAttribute("hidden");
    },
    mouseout() {
      this.$refs.payBox.setAttribute("hidden", "hidden");
    },
  },
};
</script>

<style>
