<template>
  <div class="padding-10">
    <el-row>
      <!-- 书籍封面 -->
      <el-col :span="5">
        <el-row>
          <el-col>
            <img
              :src="require('../assets/' + book.url)"
              alt="image"
              style="width: 100%"
            />
          </el-col>
        </el-row>
        <el-row type="flex" justify="center">
          <el-col class="flex justify-center padding-10">
            <el-button type="primary" style="width: 100%"
              >在线预览（前40页）</el-button
            >
          </el-col>
        </el-row>
      </el-col>
      <!-- 书籍信息 -->
      <el-col :span="19" class="padding-left-50">
        <h2>{{ title }}</h2>
        <span>作者:{{ author }}</span>
        <div class="padding-top-10 padding-bottom-10">
          <el-row>
            <el-col :span="1">
              <span>评分:</span>
            </el-col>
            <el-col :span="4">
              <el-rate
                :value="rate"
                disabled
                show-score
                text-color="#ff9900"
                score-template="{value}"
              >
              </el-rate>
            </el-col>
          </el-row>
          <el-row class="flex align-center">
            <el-col :span="1">
              <span>分类:</span>
            </el-col>
            <el-col :span="10">
              <el-tag
                type="success"
                effect="dark"
                v-for="(i, index) in 4"
                :key="index"
                style="margin-left: 10px"
                >深度学习</el-tag
              >
            </el-col>
          </el-row>
        </div>

        <!-- 书籍简介 -->
        <el-row style="padding-right: 15%; text-indent: 2em">
          <el-col>
            {{ description }}
          </el-col>
        </el-row>
        <!-- 书籍信息 -->
        <el-row style="padding-top: 50px">
          <el-col :span="12">
            <p>出版社：{{ info.press }}</p>
            <p>出版年: {{ info.publishYear }}</p>
            <p>页数: {{ info.pages }}</p>
            <p>ISBN: {{ info.ISBN }}</p>
          </el-col>
          <el-col :span="12">
            <p>提供者: {{ info.provider }}</p>
            <p>文件大小: {{ info.size }}</p>
            <p>下载次数: {{ info.count }}</p>
          </el-col>
        </el-row>
      </el-col>
    </el-row>
    <el-row>
      <el-col :span="5" class="flex justify-center">
        <el-dropdown split-button type="success" @click="handleClick">
          阿里云盘(pdf, {{ info.size }})
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item>阿里云盘</el-dropdown-item>
            <el-dropdown-item>百度云盘</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </el-col>
      <el-col :span="19" class="padding-left-50">
        <el-button type="warning" style="width: 20%">发送到邮箱</el-button>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  name: "Detail",
  data() {
    return {
      title: "Python深度学习",
      rate: 4,
      author: "Sunshine",
      description:
        "本书由Keras之父、现任Google人工智能研究员的弗朗索瓦•肖莱（François Chollet）执笔，详尽介绍了用Python和Keras进行深度学习的探索实践，涉及计算机视觉、自然语言处理、生成式模型等应用。书中包含30多个代码示例，步骤讲解详细透彻。由于本书立足于人工智能的可达性和大众化，读者无须具备机器学习相关背景知识即可展开阅读。在学习完本书后，读者将具备搭建自己的深度学习环境、建立图像识别模型、生成图像和文字等能力。",
      info: {
        press: "人民出版社",
        publishYear: "2020",
        pages: "400",
        ISBN: "47924695729",
        provider: "Sunshine",
        size: "20.21MB",
        count: "2000",
      },
      book: {
        url: "s29839337.jpg",
      }
    };
  },
  methods: {
    handleClick(){
      
    }
  },
};
</script>

