import Resource from '../views/Resource.vue'
import Detail from '../views/Detail.vue'
import VueRouter from 'vue-router'

// 创建路由器
export default new VueRouter({
    // 注意是routes，不是routers
    routes: [
        {
            path: '/',
            component: Resource,
        },
        {
            path: '/book',
            component: Resource,
        },
        {

            path: '/book/1',
            component: Detail,
        },
    ]
});